import React from "react";
import logo from "../imagenes/logoReact.png";
import "../hojas-de-estilo/Titulo.css";
import calcu from "../imagenes/calcu.png"


  function Titulo() {
    return(
      <div className='title-cont'>
        <img src={logo} className="logo" />
        <h1 className="h1-title">Calculator</h1>
        <img src={calcu} className="logo-calcu" />
      </div>
    );
  }
  
  export default Titulo;